import { useState } from "react";
import {
  AppBar,
  Avatar,
  Toolbar,
  Typography,
  IconButton,
  Drawer,
  Box,
  Container,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Popover,
  MenuItem,
  Badge,
  Switch,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import MenuIconOpen from "@mui/icons-material/MenuOpen";
import MailIcon from "@mui/icons-material/Mail";
import SettingsIcon from "@mui/icons-material/Settings";
import { useTheme } from "@mui/material/styles";
import {
  Dashboard as DashboardIcon,
  AccountBox as AccountBoxIcon,
  Notifications as NotificationsIcon,
  MoreVert as MoreIcon,
} from "@mui/icons-material";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import ExitToAppIcon from "@mui/icons-material/ExitToApp";
import LockIcon from "@mui/icons-material/Lock";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import { styled } from "@mui/material/styles";
import logo_light_doc from "../assets/images/logo/light-doc.png";
import logo_light_ngang from "../assets/images/logo/light-ngang.png";
import logo_dark_doc from "../assets/images/logo/dark-doc.png";
import logo_dark_ngang from "../assets/images/logo/dark-ngang.png";
import DrawerMenu from "../components/DrawerMenu"; // Import DrawerMenu

export default function Layout({ children, changeMode }) {
  const [isOpen, setOpen] = useState(true); // Điều khiển trạng thái của Sidenav (mở/thu gọn)
  const [isHovered, setIsHovered] = useState(false); // Trạng thái hover
  const [openPopover, setOpenPopover] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);

  const theme = useTheme(); // Lấy theme hiện tại (sáng/tối)

  // Hàm để thay đổi trạng thái Drawer khi nhấn nút
  const toggleDrawer = () => {
    setOpen((prev) => !prev); // Đảo trạng thái mở rộng/thu gọn
  };

  // Hàm để xử lý khi hover vào và ra
  const handleMouseEnter = () => {
    if (!isOpen) {
      // Nếu không mở rộng, khi hover vào thì mở rộng
      setIsHovered(true);
    }
  };

  const handleMouseLeave = () => {
    if (!isOpen) {
      // Nếu không mở rộng, khi bỏ chuột thì thu gọn
      setIsHovered(false);
    }
  };

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setOpenPopover(true);
  };

  const handleClose = () => {
    setOpenPopover(false);
  };

  const CustomSwitch = styled(Switch)(({ theme }) => ({
    width: 62,
    height: 34,
    padding: 7,
    "& .MuiSwitch-switchBase": {
      margin: 1,
      padding: 0,
      transform: "translateX(6px)",
      "&.Mui-checked": {
        color: "#fff",
        transform: "translateX(22px)",
        "& .MuiSwitch-thumb:before": {
          backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
            "#fff"
          )}" d="M4.2 2.5l-.7 1.8-1.8.7 1.8.7.7 1.8.6-1.8L6.7 5l-1.9-.7-.6-1.8zm15 8.3a6.7 6.7 0 11-6.6-6.6 5.8 5.8 0 006.6 6.6z"/></svg>')`,
        },
        "& + .MuiSwitch-track": {
          opacity: 1,
          backgroundColor: "#aab4be",
          ...theme.applyStyles("dark", {
            backgroundColor: "#8796A5",
          }),
        },
      },
    },
    "& .MuiSwitch-thumb": {
      backgroundColor: "#001e3c",
      width: 32,
      height: 32,
      "&::before": {
        content: "''",
        position: "absolute",
        width: "100%",
        height: "100%",
        left: 0,
        top: 0,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
          "#fff"
        )}" d="M9.305 1.667V3.75h1.389V1.667h-1.39zm-4.707 1.95l-.982.982L5.09 6.072l.982-.982-1.473-1.473zm10.802 0L13.927 5.09l.982.982 1.473-1.473-.982-.982zM10 5.139a4.872 4.872 0 00-4.862 4.86A4.872 4.872 0 0010 14.862 4.872 4.872 0 0014.86 10 4.872 4.872 0 0010 5.139zm0 1.389A3.462 3.462 0 0113.471 10a3.462 3.462 0 01-3.473 3.472A3.462 3.462 0 016.527 10 3.462 3.462 0 0110 6.528zM1.665 9.305v1.39h2.083v-1.39H1.666zm14.583 0v1.39h2.084v-1.39h-2.084zM5.09 13.928L3.616 15.4l.982.982 1.473-1.473-.982-.982zm9.82 0l-.982.982 1.473 1.473.982-.982-1.473-1.473zM9.305 16.25v2.083h1.389V16.25h-1.39z"/></svg>')`,
      },
      ...theme.applyStyles("dark", {
        backgroundColor: "#003892",
      }),
    },
    "& .MuiSwitch-track": {
      opacity: 1,
      backgroundColor: "#aab4be",
      borderRadius: 20 / 2,
      ...theme.applyStyles("dark", {
        backgroundColor: "#8796A5",
      }),
    },
  }));

  return (
    <Box sx={{ display: "flex", height: "100vh" }}>
      {/* Sidenav */}
      {/* Sidenav (Drawer) */}
      <Drawer
        sx={{
          width: isOpen || isHovered ? 240 : 64,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: isOpen || isHovered ? 240 : 64,
            boxSizing: "border-box",
            transition: "width 0.4s ease",
            display: "flex",
            flexDirection: "column",
            overflow: "hidden", // Đảm bảo không xuất hiện scroll không cần thiết
            backgroundColor: (theme) => theme.palette.background.paper, // Sử dụng theme
          },
        }}
        variant="permanent"
        anchor="left"
        open={isOpen || isHovered}
        onMouseEnter={handleMouseEnter} // Mở rộng khi hover
        onMouseLeave={handleMouseLeave} // Thu gọn khi không hover
      >
        <Box
          sx={{
            width: isOpen || isHovered ? 240 : 64, // Chiều rộng logo = chiều rộng của drawer
            height: 64, // Chiều cao là 64px
            backgroundColor: (theme) => theme.palette.background.paper, // Màu nền logo
            display: "flex",
            justifyContent: "center", // Căn giữa logo theo chiều ngang
            alignItems: "center", // Căn giữa logo theo chiều dọc
            borderBottom: "0.5px solid #ccc ",
            transition: "width 0.4s ease",
          }}
        >
          <img
            src={
              isOpen || isHovered
                ? logo_light_ngang.src // Logo khi mở hoặc hover
                : logo_light_doc.src // Logo khi đóng
            }
            alt="Logo"
            style={{
              maxWidth: "80%", // Chiều rộng tối đa của logo
              maxHeight: "100%", // Chiều cao tối đa của logo
              objectFit: "contain", // Đảm bảo logo không bị méo
            }}
          />
        </Box>

        <List sx={{ flexGrow: 1, transition: "width 0.4s ease" }}>
          <ListItem button>
            <ListItemIcon>
              <DashboardIcon
                sx={{ color: (theme) => theme.palette.primary.main }}
              />{" "}
              {/* Điều chỉnh màu sắc icon theo theme */}
            </ListItemIcon>
            <ListItemText primary={isOpen || isHovered ? "Dashboard" : ""} />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <AccountBoxIcon
                sx={{ color: (theme) => theme.palette.primary.main }}
              />{" "}
              {/* Điều chỉnh màu sắc icon theo theme */}
            </ListItemIcon>
            <ListItemText primary={isOpen || isHovered ? "Profile" : ""} />
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <SettingsIcon
                sx={{ color: (theme) => theme.palette.primary.main }}
              />{" "}
              {/* Điều chỉnh màu sắc icon theo theme */}
            </ListItemIcon>
            <ListItemText primary={isOpen || isHovered ? "Settings" : ""} />
          </ListItem>
        </List>

        <List
          sx={{
            position: "fixed",
            padding: 1,
            bottom: 0,
            width: isOpen || isHovered ? "239px" : "63px",
            backgroundColor: (theme) => theme.palette.background.paper, // Sử dụng theme cho màu nền
            borderTop: "0.5px solid #ccc ",
            transition: "width 0.1s ease",
          }}
        >
          <ListItem
            button
            sx={{
              display: "flex",
              justifyContent: isOpen || isHovered ? "flex-start" : "center", // Căn trái khi mở rộng, căn giữa khi thu gọn
              alignItems: "center", // Căn giữa theo chiều dọc
              padding: 1,
            }}
          >
            <ListItemIcon sx={{ display: "flex", justifyContent: "center" }}>
              <Avatar alt="HN" src="" />
            </ListItemIcon>
            {(isOpen || isHovered) && (
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  marginLeft: 1,
                }}
              >
                <Typography variant="body2">Huy Nguyen</Typography>
                <Typography variant="body2" color="textSecondary">
                  Quản Trị Viên
                </Typography>
              </Box>
            )}
            {(isOpen || isHovered) && (
              <IconButton onClick={handleClick} sx={{ marginLeft: "auto" }}>
                <MoreVertIcon />
              </IconButton>
            )}
          </ListItem>
        </List>

        {/* Popover for 3 dots menu */}
        <Popover
          open={openPopover}
          anchorEl={anchorEl}
          onClose={handleClose}
          anchorOrigin={{
            vertical: "top",
            horizontal: "left",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
          sx={{
            boxShadow: (theme) => theme.shadows[3], // Sử dụng shadow từ theme
            borderRadius: (theme) => theme.shape.borderRadius, // Sử dụng borderRadius từ theme
            transition: "width 0.4s ease",
          }}
        >
          <Box sx={{ width: "100%" }}>
            <MenuItem
              onClick={handleClose}
              sx={{
                display: "flex",
                alignItems: "center",
                padding: 3,
                borderRadius: "6px",
                transition: "width 0.4s ease",

                "&:hover": {
                  backgroundColor: (theme) => theme.palette.action.hover, // Sử dụng màu hover từ theme
                  color: (theme) => theme.palette.text.primary, // Màu chữ khi hover
                },
              }}
            >
              <AccountCircleIcon
                sx={{
                  marginRight: 1,
                  color: (theme) => theme.palette.primary.main,
                }}
              />
              <Typography sx={{ fontWeight: 500 }}>
                Chỉnh Sửa Tài Khoản
              </Typography>
            </MenuItem>
            <MenuItem
              onClick={handleClose}
              sx={{
                display: "flex",
                alignItems: "center",
                padding: 3,
                borderRadius: "6px",
                transition: "background-color 0.3s ease",
                "&:hover": {
                  backgroundColor: (theme) => theme.palette.action.hover, // Sử dụng màu hover từ theme
                  color: (theme) => theme.palette.text.primary, // Màu chữ khi hover
                },
              }}
            >
              <LockIcon
                sx={{
                  marginRight: 1,
                  color: (theme) => theme.palette.warning.main,
                }}
              />
              <Typography sx={{ fontWeight: 500 }}>Đổi Mật Khẩu</Typography>
            </MenuItem>
            <Divider
              sx={{ my: 1, borderColor: (theme) => theme.palette.divider }}
            />{" "}
            {/* Sử dụng divider color từ theme */}
            <MenuItem
              onClick={handleClose}
              sx={{
                display: "flex",
                alignItems: "center",
                justifyContent: "flex-end",
                fontSize: "12px",
                padding: 2,
                borderRadius: "6px",
                transition: "width 0.4s ease",
                "&:hover": {
                  backgroundColor: (theme) => theme.palette.action.hover, // Sử dụng màu hover từ theme
                  color: (theme) => theme.palette.text.primary, // Màu chữ khi hover
                },
              }}
            >
              <ExitToAppIcon
                sx={{
                  marginRight: 1,
                  color: (theme) => theme.palette.error.main,
                }}
              />
              <Typography sx={{ fontWeight: 500 }}>Đăng Xuất</Typography>
            </MenuItem>
          </Box>
        </Popover>
      </Drawer>

      {/* Header */}
      <Box
        component="header"
        sx={{
          width: `calc(100% - ${isOpen || isHovered ? 240 : 64}px)`, // Điều chỉnh độ rộng khi thu gọn
          backgroundColor: (theme) => theme.palette.background.paper, // Sử dụng background từ theme
          padding: 2,
          position: "fixed",
          top: 0,
          left: `${isOpen || isHovered ? 240 : 64}px`, // Điều chỉnh vị trí khi thu gọn
          zIndex: 1201, // Đảm bảo Header luôn nằm trên các phần tử khác
        }}
      >
        <AppBar
          position="fixed"
          sx={{
            width: `calc(100% - ${isOpen || isHovered ? 240 : 64}px)`,
            backgroundColor: (theme) => theme.palette.primary.main, // Sử dụng màu chính từ theme cho AppBar
            boxShadow: 3, // Áp dụng shadow từ theme
          }}
        >
          <Toolbar>
            <IconButton
              color="inherit"
              edge="start"
              sx={{ mr: 2 }}
              onClick={toggleDrawer}
            >
              {isOpen || isHovered ? <MenuIconOpen /> : <MenuIcon />}{" "}
              {/* Điều kiện thay đổi icon */}
            </IconButton>

            <Box sx={{ flexGrow: 1 }} />
            <Box sx={{ display: { xs: "none", md: "flex" } }}>
              <IconButton size="large" color="inherit">
                <Badge badgeContent={0} color="error">
                  <MailIcon />
                </Badge>
              </IconButton>
              <IconButton size="large" color="inherit">
                <Badge badgeContent={0} color="error">
                  <NotificationsIcon />
                </Badge>
              </IconButton>
            </Box>
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <CustomSwitch
                checked={theme.palette.mode === "dark"} // Kiểm tra chế độ hiện tại
                onChange={changeMode} // Hàm đổi chế độ
              />
            </Box>
          </Toolbar>
        </AppBar>
        <Box sx={{ marginTop: 8 }}></Box>{" "}
        {/* Để tránh bị che khuất bởi AppBar */}
      </Box>

      {/* Main Content */}
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          padding: 2,
          backgroundColor: (theme) => theme.palette.background.default, // Sử dụng background từ theme
          minHeight: "100vh",
          width: `calc(100% - ${isOpen || isHovered ? 240 : 64}px)`, // Điều chỉnh độ rộng khi thu gọn
          marginTop: "64px", // Cung cấp không gian cho header cố định
          transition: "width 0.3s ease", // Thêm hiệu ứng chuyển đổi khi thu gọn hoặc mở rộng
        }}
      >
        <Container maxWidth="xl">{children}</Container>
      </Box>

      {/* Footer */}
      <Box
        component="footer"
        sx={{
          width: `calc(100% - ${isOpen || isHovered ? 240 : 64}px)`, // Điều chỉnh độ rộng khi thu gọn
          backgroundColor: (theme) => theme.palette.background.paper, // Sử dụng màu nền từ theme
          padding: 2,
          position: "fixed",
          bottom: 0,
          left: `${isOpen || isHovered ? 240 : 64}px`, // Điều chỉnh vị trí khi thu gọn
          transition: "left 0.3s ease, width 0.3s ease", // Thêm hiệu ứng chuyển động khi thu gọn hoặc mở rộng
        }}
      >
        <Typography variant="body2" color="text.secondary" align="center">
          © 2024 Nguyễn Minh Huy. All rights reserved.
        </Typography>
      </Box>
    </Box>
  );
}
